<template>
  <aside class="filter">

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model.number="currentCategoryId">
            <option value="0">Все категории</option>
            <option :value="category.id" v-for="category in categories" :key="category.id"> {{ category.title }} </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <div class="check-list">
          <div class="check-list__item" v-for="material in materials" :key="material.id">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="material" :id="material.id"
                v-model="currentMaterialId[material.id]" v-on:change.stop>
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </div>
        </div>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <div class="check-list">
          <div class="check-list__item" v-for="season in seasons" :key="season.id">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="collection" :id="season.id"
                v-model="currentSeasonId[season.id]">
              <span class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </div>
        </div>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <div class="colors colors--black">
          <div class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="checkbox" name="color" :id="color.id"
                v-model.number="currentColorId[color.id]">
              <span class="colors__value" :style="{ backgroundColor: color.code }">
              </span>
            </label>
          </div>
        </div>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" v-if="currentPriceFrom != 0 || currentPriceTo != 0 || currentCategoryId != 0 || currentMaterialId.length != 0 || currentSeasonId.length != 0 || currentColorId.length != 0 " type="button" @click.prevent="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColorId: [],
      currentMaterialId: [],
      currentSeasonId: [],
      categoriesData: null,
      materialsData: null,
      seasonsData: null,
      colorsData: null
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'materialIds', 'seasonIds', 'colorIds'],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    materials() {
      return this.materialsData ? this.materialsData.items : [];
    },
    seasons() {
      return this.seasonsData ? this.seasonsData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    materialIds(value) {
      this.currentMaterialId = value;
    },
    seasonIds(value) {
      this.currentSeasonId = value;
    },
    colorIds(value) {
      this.currentColorId = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:materialIds', this.currentMaterialId);
      this.$emit('update:seasonIds', this.currentSeasonId);
      this.$emit('update:colorIds', this.currentColorId);
    },
    reset() {
        this.$emit('update:priceFrom', 0);
        this.$emit('update:priceTo', 0);
        this.$emit('update:categoryId', 0);
        this.$emit('update:materialIds', []);
        this.$emit('update:seasonIds', []);
        this.$emit('update:colorIds', []);
    },
    loadCategories() {
      axios.get(API_BASE_URL + `/api/productCategories`)
        .then(response => this.categoriesData = response.data);
    },
    loadMaterials() {
      axios.get(API_BASE_URL + `/api/materials`)
        .then(response => this.materialsData = response.data);
    },
    loadSeasons() {
      axios.get(API_BASE_URL + `/api/seasons`)
        .then(response => this.seasonsData = response.data);
    },
    loadColors() {
      axios.get(API_BASE_URL + `/api/colors`)
        .then(response => {
          this.colorsData = response.data;
          this.colorsData.items.forEach(color => {
            if (color.code == "#ffffff") {
              color.code = "#F0F0F0"
            }
          })
        });

    },
  },
  created() {
    this.loadCategories(),
      this.loadMaterials(),
      this.loadSeasons(),
      this.loadColors();
      this.currentCategoryId=this.categoryId
  }
};
</script>
