import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartLoading: false,
    cartProductLoading: false,
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      console.log(orderInfo)
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { id, amount }) {
      const item = state.cartProducts.find(item => item.id === id);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, id) {
      state.cartProducts = state.cartProducts.filter(item => item.id !== id);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
      items.forEach(item => {
        if (item.color.color.code == "#ffffff") {
          item.color.color.code = "#F0F0F0"
        }
      })
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          colorId: item.color.id,
          sizeId: item.size.id,
          amount: item.quantity
        }
      })
    }
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
        const color = state.cartProductsData.find(p => p.color.id === item.colorId).color;
        const size = state.cartProductsData.find(p => p.size.id === item.sizeId).size;
        const id = state.cartProductsData.find(p => p.size.id === item.sizeId && p.color.id === item.colorId && product.id === item.productId).id;
        return {
          ...item,
          product: {
            ...product,
          },
          color: {
            ...color,
          },
          id: id,
          size: {
            ...size,
          },
        }
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    }
  },
  actions: {
    async loadOrderInfo(context, orderId) {
      const response = await axios
        .get(API_BASE_URL + `/api/orders/` + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        });
      context.commit('updateOrderInfo', response.data);
    },
    loadCart(context) {
      context.state.cartLoading = true;
      context.state.cartProductLoading = true;
      clearTimeout(this.loadCartTimer);
      this.loadCartTimer = setTimeout(async () => {
        return await axios
          .get(API_BASE_URL + `/api/baskets`, {
            params: {
              userAccessKey: context.state.userAccessKey
            }
          })
          .then(response => {
            if (!context.state.userAccessKey) {
              localStorage.setItem('userAccessKey', response.data.user.accessKey);
              context.commit('updateUserAccessKey', response.data.user.accessKey);
            }
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          })
          .then(() => {
            context.state.cartLoading = false,
              context.state.cartProductLoading = false
          });
      }, 5000);
    },
    async addProductToCart(context, { productId, colorId, sizeId, amount }) {
      await (new Promise(resolve => setTimeout(resolve, 2000)));
      context.state.cartLoading = true;
      const response = await axios
        .post(API_BASE_URL + `/api/baskets/products`, {
          productId: productId,
          colorId: colorId,
          sizeId: sizeId,
          quantity: amount
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        });
      context.commit('updateCartProductsData', response.data.items);
      context.commit('syncCartProducts');
      return context.state.cartLoading = false;
    },
    async updateCartProductAmount(context, { id, amount }) {
      context.commit('updateCartProductAmount', { id, amount });
      if (amount < 1) {
        return;
      }
      try {
        const response = await axios
          .put(API_BASE_URL + `/api/baskets/products`, {
            basketItemId: id,
            quantity: amount
          }, {
            params: {
              userAccessKey: context.state.userAccessKey
            }
          });
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      } catch {
        context.commit('syncCartProducts');
      }
    },
    async deleteProductToCart(context, id) {
      console.log(id)
      const response = await axios
        .delete(API_BASE_URL + `/api/baskets/products`, {
          data: {
            basketItemId: id,
          },
          params: {
            userAccessKey: context.state.userAccessKey,
          }
        });
      context.commit('updateCartProductsData', response.data.items);
      context.commit('syncCartProducts');
    }
  }
});
