<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{ name: 'product', params: { id: product.id } }">
      <img :src="srcImg" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>

    <div class="colors colors--black">
      <div class="colors__item" v-for="color in product.colors" :key="color.id">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="color.id" v-on:click="ChangeImage(color)"
            v-model="checkedColor">
          <span class="colors__value" :style="{ backgroundColor: color.color.code }">
          </span>
        </label>
      </div>
    </div>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      checkedColor: null,
      srcImg: null,
    }
  },
  filters: {
    numberFormat
  },
  props: ['product'],
  methods: {
    ChangeImage(color) {
      if (color.gallery != null) {
        this.srcImg = color.gallery[0].file.url;
      } else {
        this.srcImg = null;
      }
    }
  },
  created() {
    this.checkedColor = this.product.colors[0].id,
      this.ChangeImage(this.product.colors[0])
  }
};

</script>

